import styled from "styled-components";

import { InnerWrap, Text, Title2 } from "../../assets/styles/component";

import Format from "../../assets/images/Formats.webp";
import Logo_img_1 from "../../assets/images/intro_Img_1.webp";
import Logo_img_2 from "../../assets/images/intro_Img_2.webp";
import Logo_img_3 from "../../assets/images/intro_img_3.webp";
import Logo_img_4 from "../../assets/images/intro_img_4.webp";

import { useEffect, useRef, useState } from "react";

const ScrollWrapper = styled.div`
  width: 100vw;
`;

const SFormats = styled.section`
  position: sticky;
  top: 0;
  transform: translateY(-100px);
  background: radial-gradient(
      56.92% 42.11% at 111.57% 109.48%,
      #06a5d8 20.47%,
      rgba(0, 51, 233, 0.637795) 59.04%,
      rgba(41, 1, 157, 0) 100%
    ),
    radial-gradient(
      71.73% 87.75% at -17.08% -20.39%,
      #ecbbc3 0%,
      rgba(200, 62, 198, 0.637795) 57.81%,
      rgba(160, 9, 230, 0) 100%
    ),
    #29009c;
  border-radius: 106px;
  padding: 150px 0;
  /* top: 110px; */
  left: 0;
  width: 100vw;
  height: fit-content;
  z-index: 100;

  & > div {
    width: 100vw;
    max-width: unset;
    display: flex;
    flex-direction: column;
    align-items: center;

    & > p {
      text-align: center;
      margin-bottom: 43px;
    }
  }

  @media (max-width: 1000px) {
    padding: 70px 0;
    border-radius: 60px;
  }
`;

const Title = styled(Title2)`
  margin-bottom: 750px;
  top: 1vw;

  @media (max-width: 1300px) {
    margin-bottom: 550px;
  }

  @media (max-width: 1000px) {
    margin-bottom: 70px;
  }
`;

const FormatList = styled.div`
  position: absolute;
  top: 200px;
  align-self: flex-start;
  height: 480px;
  width: 100vw;
  margin-bottom: 70px;

  transition: 0.1s linear;
  
  @media (max-width: 1300px) {
    top: 150px;
  }

  @media (max-width: 1000px) {
    gap: 27px;
    margin-bottom: 70px;
    height: 235px;
  }

  main {
    overflow: hidden;
    display: flex;
    padding: 20px 10%;
    height: 480px;
    gap: 65px;
    top: calc(50% - 11vw);
    transition: 0.5s ease-in-out;
  }

  & + div {
    bottom: 0;
    text-align: center;

    p {
      margin-bottom: 20px;
    }
  }
`;

const Frame = styled.div`
  overflow: hidden;
  width: 760px;
  height: 480px;
  min-width: 760px;
  min-height: 480px;

  outline: 20px solid #ffffff22;
  border-radius: 70px;

  background: radial-gradient(
    240.7% 80.8% at 107.77% 65.48%,
    #a927ca 0%,
    #d72f75 41.67%,
    #e79079 76.04%,
    #e3592f 98.44%
  );

  img {
    width: 100%;
  }

  @media (max-width: 1300px) {
    width: 500px;
    height: 315px;
    min-width: 500px;
    min-height: 315px;
    outline: 9px solid rgba(255, 255, 255, 0.1);
    border-radius: 30px;

    img {
      margin-bottom: -5px;
    }
  }

  @media (max-width: 1000px) {
    width: 317px;
    min-width: 317px;
    min-height: 200px;
    height: fit-content;
  }
`;

const Button = styled.button`
  border: 0;
  cursor: pointer;
  font-weight: 400;
  font-size: 26px;
  line-height: 120%;
  text-align: center;
  letter-spacing: 0.01em;
  color: #29009c;
  background: #ffffff;
  outline: 10px solid rgba(255, 255, 255, 0.1);
  border-radius: 73px;
  padding: 33px 96px;
  transition: 0.25s ease-in-out;

  &:hover {
    transform: scale(1.01);
    outline: 14px solid rgba(255, 255, 255, 0.1);
  }
`;

const Formats = ({ language }) => {
  const list = useRef(null);
  const formats = useRef(null);

  const topSticky = useRef(null);
  const bottomSticky = useRef(null);

  var g_containerInViewport;

  useEffect(() => {
    setStickyContainersSize();
    window.addEventListener("wheel", wheelHandler);

    return () => {
      window.removeEventListener("wheel", wheelHandler);
    };
  }, []);

  function setStickyContainersSize() {
    document
      .querySelectorAll(".sticky-container")
      .forEach(function (container) {
        const stikyContainerHeight =
          container.querySelector("main").scrollWidth - window.innerHeight;

        container.setAttribute(
          "style",
          "height: " + stikyContainerHeight + "px"
        );

        formats.current.setAttribute(
          "style",
          "height: " + stikyContainerHeight + "px"
        );
      });
  }

  function isElementInViewport(el) {
    const rect = el.getBoundingClientRect();
    return (
      rect.top - 250 <= 0 &&
      rect.bottom - document.documentElement.clientHeight > 0
    );
  }

  function wheelHandler(evt) {
    const containerInViewPort = Array.from(
      document.querySelectorAll(".sticky-container")
    ).filter(function (container) {
      return isElementInViewport(container);
    })[0];

    if (!containerInViewPort) {
      topSticky.current.style.position = "relative";
      bottomSticky.current.style.position = "relative";
      return;
    }

    let coordY =
      containerInViewPort.getBoundingClientRect().top + window.pageYOffset;

    var isPlaceHolderBelowTop =
      containerInViewPort.offsetTop - 300 < document.documentElement.scrollTop;
    var isPlaceHolderBelowBottom =
      coordY + containerInViewPort.offsetHeight >
      document.documentElement.scrollTop;

    let g_canScrollHorizontally =
      isPlaceHolderBelowTop && isPlaceHolderBelowBottom;

    if (g_canScrollHorizontally) {
      containerInViewPort.querySelector("main").scrollLeft += evt.deltaY / 1.3;
    }
  }

  return (
    <ScrollWrapper ref={formats}>
      <SFormats>
        <InnerWrap>0
          <Title ref={topSticky}>Доступные форматы</Title>

          <FormatList ref={list} className="sticky-container">
            <main>
              <Frame>
                <img src={Format} alt="Format_1" loading="lazy" />
              </Frame>
              <Frame>
                <img src={Logo_img_1} alt="Format_2" loading="lazy" />
              </Frame>
              <Frame>
                <img src={Format} alt="Format_3" loading="lazy" />
              </Frame>
              <Frame>
                <img src={Logo_img_2} alt="Format_1" loading="lazy" />
              </Frame>
              <Frame>
                <img src={Format} alt="Format_2" loading="lazy" />
              </Frame>
              <Frame>
                <img src={Logo_img_3} alt="Format_3" loading="lazy" />
              </Frame>
              <Frame>
                <img src={Logo_img_4} alt="Format_3" loading="lazy" />
              </Frame>
            </main>
          </FormatList>

          <div ref={bottomSticky}>
            <Text size="26">
              Узнайте, какой формат подойдет <br /> именно для вашего продукта:
            </Text>
            <Button>Начать общение</Button>
          </div>
        </InnerWrap>
      </SFormats>
    </ScrollWrapper>
  );
};

export default Formats;
